<template>
  <div class="issue">
    <div class="header"></div>
    <div class="content">
      <div class="input" >
        <div contenteditable="true" class="input-info" ref="summary" @click="handleFocus"></div>
        <div class="input-cont">
          <div class="img-box">
            <img src="" alt="" ref="imgshow">
          </div>
          <div class="change">
            <input type="file" class="change-file border" ref="img" @change="handleImgChange" accept="image/*" multiple="multiple" @click="handleFocus"/>
          </div>
        </div>
      </div>
      <div class="link">
        <img src="../../../static/img/link2.png" alt="">
        <input placeholder="...." ref="title" @click="handleFocus">
      </div>
      <div class="link">
        <img src="../../../static/img/ding.png" alt="">
        <input type="text" placeholder="" ref="localtion" @click="handleFocus"> 
      </div>
      <div class="link">
        <img src="../../../static/img/dd.png" alt="">
        <select name="" id="" ref="select" @click="handleFocus">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>
      <p v-show="isp" class="check"></p>
      <div class="btn" @click="handleInputClick">
        
      </div>
    </div>
    <bottom class="bottom"></bottom>
  </div>
</template>

<script>
  import Bottom from 'components/common/bottom'
  import axios from 'axios'
  export default {
    name: 'issue',
    data () {
      return {
        title: '',
        summary: '',
        city: '',
        isBtn: false,
        isp: false
      }
    },
    components: {
      Bottom
    },
    methods: {
      handleInputClick () {
        if (this.isBtn) {
          this.title = this.$refs.title.value
          this.summary = this.$refs.summary.innerHTML
          this.city = this.$refs.select.value
          if (this.title && this.summary && this.city) {
            let file = this.$refs.img.files[0]
            let param = new FormData()
            param.append('file', file, file.name)
            param.append('title', this.title)
            param.append('summary', this.summary)
            param.append('city', this.city)
            let config = {
              headers: { 'Content-Type': 'multipart/form-data' }
            }
            axios.post('/api/circle/add', param, config)
            .then((res) => {
              if (res) {
                res = (res.data) ? res.data : null
                if (res && res.success) {
                  this.$push('/running')
                }
              }
            })
          } else {
            this.isp = true
          }
        } else {
          this.$router.push('/login')
        }
      },
      handleImgChange (e) {
        var that = e.target
        var that_ = this
        var file = that.files[0]
        var reader = new FileReader()   
        reader.readAsDataURL(file)
        if(file.name.indexOf("jpg") != -1 || file.name.indexOf("JPG") != -1 || file.name.indexOf("PNG") != -1 || file.name.indexOf("png") != -1) {
            reader.onload = function (e) {
              that_.$refs.imgshow.src = e.target.result
            }
        } else {
          alert('')
        }
      },
      handleFocus () {
        this.isp = false
      }
    },
    mounted () {
      if (!document.cookie.userid) {
        this.isBtn = true
      } else {
        console.log(document.cookie)
        this.isBtn = false
      }
    }
  }
</script>


<style scoped>
  .issue {
    background: #e0e0e0;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
  }
  .header {
    text-align: center;
    background: #42c69a;
    line-height: .88rem;
    font-size: .32rem;
    color: #fff;
  }
  .content {
    padding: .88rem .4rem 0;
  }
  .input {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 4.3rem;
    width: 100%;
    border-radius: 5px;
    background: #fff;
    box-sizing: border-box;
    padding: .4rem;
    font-size: .28rem;
    color: #333;
  }
  .input-cont {
    height: 1.4rem;
  }
  .input-cont .img-box {
    float: left;
    width: 1.4rem;
    height: 0;
    padding-bottom: 1.4rem;
    margin-right: .2rem;
  }
  .img-box img {
    width: 100%;
  }
  .change-file {
    opacity: 0;
    height: 100%;
    width: 100%;
  }
  .change {
    height: 1.4rem;
    width: 1.4rem;
    background: url(../../../static/img/jia.png) no-repeat center;
    background-size: 100%, 100%;
    float: left;
  }
  .link {
    width: 100%;
    height: .8rem;
    background: #fff;
    display: flex;
    margin-top: .2rem;
    align-items: center;
    border-radius: 5px;
    padding: 0 .3rem;
    box-sizing: border-box;
  }
  .link img {
    width: .46rem;
    height: .46rem;
    margin-right: .2rem;
  }
  .link span {
    line-height: .46rem;
    font-size: .24rem;
    color: #333;
  }
  .btn {
    height: .8rem;
    width: 3rem;
    border-radius: 5px;
    background: #42c69a;
    text-align: center;
    line-height: .8rem;
    color: #fff;
    margin: .5rem auto;
  }
  .bottom {
    position: absolute;
    bottom: 0;
    width: 100%;
  }
  .input-info {
    height: 2rem;
    overflow: hidden;
  }
  .check {
    color: red;
    text-align: center;
    margin-top: .4rem;
  }
</style>




